.burgers {
    margin: 72px auto;
    @include clr; }
.prices {
	width: 100%;
	box-sizing: border-box;
	img {
		max-width: 100%; }
	a {
		cursor: pointer; }
	a:hover {
		border: 1px solid #eeeeee;
		transform: scale(1.05); } }

.price {
    display: inline-block;
    width: 22%;
    float: left;
    box-sizing: border-box;
    margin: 0;
    margin-right: 17%;
    padding: 50px 0 0 0;
    border: 1px;
    @include clr;
    &__img {
        max-width: 100%;
        height: 150px;
        img {
            width: 100%; } }
    &__title {
        display: block;
        font-size: 22.63px;
        font-family: "Rubik";
        color: #f39c22;
        line-height: 0.692;
        text-align: center;
        margin: 7% 0; }
    &__text {
        display: block;
        font-size: 12.18px;
        font-family: "Martel";
        color: #0b1d2c;
        line-height: 1.286;
        text-align: center; }
    &__button {
        height: 30px;
        width: 100%;
        background-color: #e1e1e1; } }

/* For a 3-column grid */
.price:nth-child(3n+3) {
    margin-right: 0;
    float: right; }
