html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

.icon-facebook {
  width: 0.53em;
  height: 1em;
  fill: #000;
}

body {
  background: #FFF;
  font-size: 50px;
  font-size: calc(16px + (50 - 16) * ((100vw - 600px) / (1000 - 600)));
  line-height: 1.4;
}

@media (max-width: 600px) {
  body {
    font-size: 16px;
  }
}

@media (min-width: 1000px) {
  body {
    font-size: 50px;
  }
}

.header {
  margin: 50px auto;
}

.header:after {
  content: " ";
  display: table;
  clear: both;
}

.header__container {
  width: 845px;
  margin: 0 auto;
  position: relative;
}

.header__logo {
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
  cursor: pointer;
  text-decoration: none;
  font-size: 26px;
  font-family: "Martel";
  line-height: 0.669;
}

.header__logo:hover {
  font-weight: bold;
}

.header__nav {
  position: absolute;
  top: 50%;
  right: 0;
  transform: translateY(-50%);
  margin-top: -7px;
}

.header__nav a {
  text-decoration: none;
  padding: 5px 0;
  border-bottom: 3px solid #ffffff;
  font-size: 16px;
  font-family: "Martel";
  color: #0e1d2b;
  line-height: 1.087;
  margin-left: 15px;
}

.header__nav a:hover {
  font-weight: bold;
  border-bottom: 3px solid #ffa326;
}

.logo__part1 {
  color: #0e1d2b;
}

.logo__part2 {
  color: #ffa326;
}

.footer {
  background-color: #050b10;
}

.footer:after {
  content: " ";
  display: table;
  clear: both;
}

.copywrite, .site {
  font-size: 12px;
  font-family: "Martel";
  color: #ffa326;
  line-height: 3.821;
  text-align: center;
}

.copywrite {
  float: left;
}

.site {
  float: right;
}

.hero {
  margin-bottom: 50px;
}

.hero:after {
  content: " ";
  display: table;
  clear: both;
}

.hero__part1, .hero__part2 {
  background-position: 100% 100%;
  background-size: cover;
  background-repeat: no-repeat;
  width: 50%;
  float: left;
}

.hero__backg1, .hero__backg2 {
  height: 395px;
  opacity: 0.92;
  position: relative;
}

.hero__backg1 {
  background-color: #011627;
}

.hero__backg2 {
  background-color: #ff9f1c;
}

.hero__wrap1, .hero__wrap2 {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  width: 422px;
}

.hero__wrap1 {
  right: 0;
}

.hero__wrap2 {
  left: 0;
}

.hero__title1, .hero__title2 {
  font-size: 65px;
  font-family: "Rubik";
  line-height: 0.923;
}

.hero__title1 {
  color: #ffa326;
}

.hero__title2 {
  color: #ffffff;
  margin-left: 17%;
}

.hero__text1, .hero__text2 {
  display: block;
  font-size: 14px;
  font-family: "Martel";
  line-height: 1.429;
  padding: 10px 0;
}

.hero__text1 {
  color: #ffffff;
}

.hero__text2 {
  color: #0d1e2d;
  margin-left: 17%;
}

.showme {
  margin-left: 10px;
  height: 38px;
  width: 160px;
  background-color: #ffffff;
  font-size: 16px;
  font-family: "Martel";
  color: #0d1e2d;
  line-height: 1.125;
  font-weight: bold;
  box-shadow: 10px 5px 5px #3e3e3e;
}

.burgers {
  margin: 72px auto;
}

.burgers:after {
  content: " ";
  display: table;
  clear: both;
}

.prices {
  width: 100%;
  box-sizing: border-box;
}

.prices img {
  max-width: 100%;
}

.prices a {
  cursor: pointer;
}

.prices a:hover {
  border: 1px solid #eeeeee;
  transform: scale(1.05);
}

.price {
  display: inline-block;
  width: 22%;
  float: left;
  box-sizing: border-box;
  margin: 0;
  margin-right: 17%;
  padding: 50px 0 0 0;
  border: 1px;
}

.price:after {
  content: " ";
  display: table;
  clear: both;
}

.price__img {
  max-width: 100%;
  height: 150px;
}

.price__img img {
  width: 100%;
}

.price__title {
  display: block;
  font-size: 22.63px;
  font-family: "Rubik";
  color: #f39c22;
  line-height: 0.692;
  text-align: center;
  margin: 7% 0;
}

.price__text {
  display: block;
  font-size: 12.18px;
  font-family: "Martel";
  color: #0b1d2c;
  line-height: 1.286;
  text-align: center;
}

.price__button {
  height: 30px;
  width: 100%;
  background-color: #e1e1e1;
}

/* For a 3-column grid */
.price:nth-child(3n+3) {
  margin-right: 0;
  float: right;
}

.waitsec {
  background-position: 50% 50%;
  background-repeat: no-repeat;
  background-size: cover;
}

.waitsec:after {
  content: " ";
  display: table;
  clear: both;
}

.waitsec__backg {
  opacity: 0.92;
  background: linear-gradient(#0b1d2c, #ff9f1c);
}

.waitsec__wrap {
  height: 404px;
  position: relative;
}

.waitsec__title, .waitsec__table {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}

.waitsec__title {
  font-size: 60px;
  font-family: "Rubik";
  color: #ffffff;
  line-height: 1;
  text-align: center;
}

.waitsec__table {
  right: 0;
  border: 3px solid #ff9f1c;
  padding: 30px;
}

.waitsec__table table tr {
  padding: 25px;
}

.waitsec__table table tr td {
  font-size: 16px;
  font-family: "Martel";
  color: #ffffff;
  line-height: 0.979;
  text-align: left;
  padding: 7px;
}

.photos img {
  width: 100%;
}

.block {
  display: flex;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

.container {
  margin: 0 auto;
  max-width: 725px;
}

.block__title, .block__text {
  display: block;
  margin: 0 auto;
  text-align: center;
}

.block__title {
  margin-top: 50px;
  font-size: 60px;
  font-family: "Rubik";
  color: #0d1624;
  line-height: 1;
}

.block__text {
  margin-bottom: 50px;
  padding: 15px;
  font-size: 12.18px;
  font-family: "Martel";
  color: #0b1d2c;
  line-height: 1.286;
  text-align: center;
}
